html, body {
    user-select: none;
    height: 100%;
    padding: 0;
    margin: 0;
    font-size: 14px;
}

.selectable {
    user-select: text;
}

.input-group-addon:not(:first-child):not(:last-child) {
    border-right: 0;
}

.joint-highlight-stroke {
    fill: transparent;
    stroke: red;
    stroke-width: 2px;
}

.navbar.top {
    z-index: 1;
}

.navbar-brand > svg, #about svg {
    margin-top: -6px;
    path {
        transition: all 0.9s;
    }
    path:hover {
        fill: #fff;
        stroke-width: 2%;
        transition: all 0.1s;
    }
}

.ifml-image {
    background: url(../img/linen_bg_tile.jpg);
}

.joint-type-ifml .labels .label:nth-child(1) path,
.joint-type-ifml .labels .label:nth-child(2) path, {
    visibility: hidden;
}

.joint-type-ifml .labels .label:nth-child(3) rect {
    visibility: hidden;
}

.editor {
    @extend .ifml-image;
    position: absolute;
    display: block;
    visibility: visible;
    top: 50px;
    left: 0;
    right: 0;
    bottom: 0;

    .joint-type-ifml .link-tool > .tool-options,
    .joint-type-pcn .link-tool > .tool-options {
        display: block;
    }

    .sidebar {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        width: 200px;
        overflow-y: auto;
        overflow-x: hidden;
        background: white;
    }

    .joint-paper {
        background-color: transparent;
    }

    .board {
        position: absolute;
        left: 200px;
        top: 0;
        right: 0;
        bottom: 0;
        overflow: hidden;

        .menu {
            position: absolute;
            left: 0px;
            bottom: 0px;
            padding: 5px;
        }
    }

    .board:focus {
        outline: none;
    }

    .board.not-interactive .joint-element {
        cursor: auto;
    }
}

.tab-content > .tab-pane.content {
  padding-top: 60px;
}

.tab-content > .tab-pane.editor {
    display: block;
    margin-bottom: 200%;
    margin-top: -200%;
}

.tab-content > .tab-pane.editor.active {
    margin-bottom: 0;
    margin-top: 0;
}

.ifbrowser{
    position: relative;
    margin: 1px;
    padding-top: 120%;
    @media (min-width: 768px) {
        padding-top: 62.5%;
    }
    @media (min-width: 992px) {
        padding-top: 56.25%;
    }

    iframe {
        border: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
    }
}

.ifclient{
    position: relative;
    margin: 1px;
    padding-top: 120%;
    @media (min-width: 768px) {
        padding-top: 62.5%;
    }
    @media (min-width: 992px) {
        padding-top: 56.25%;
    }

    iframe {
        border: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
    }
}

.ifbrowser-baseaddress {
    padding-right: 0.125em;
}

.ifbrowser-address {
    border-left: 0;
    padding-left: 0.125em;
}

.ifmobile {
    .ifmobile-wrapper {
        position: relative;
        padding-top: 160%;
        margin-bottom: 15px;
    }

    .ifmobile-speaker {
        background-color: #ddd;
        height: 5px;
        border-radius: 2px;
    }

    iframe {
        background-color: white;
        border: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
    }
}

#about {
  margin-top: 25px;
}

.db-table {
    height: calc(100vh - 265px);
    overflow: auto;
}
